<template>
  <div class="container">
    <h3>登入</h3>
    <form @submit.prevent="login">
      <div class="form-group"  >
        <label>帳號</label>
        <b-form-input
          class="form-control form-control-lg"
          v-model="usr"
          placeholder="請輸入您的帳號" 
        />
      </div>

      <div class="form-group">
        <label>密碼</label>
        <b-form-input
          class="form-control form-control-lg"
          v-model="psw"
          placeholder="請輸入您的密碼"
        />
      </div>
      <br>
      <button type="submit" class="btn btn-primary">Submit</button>
    </form>

  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      rows: [],
      usr: '',
      psw: '',
      showPassword: false,
    };
  },
  methods: {
    login(){
      // 從 Google Sheets 取得資料，並找出指定時間的文章
      axios.get('https://script.google.com/macros/s/AKfycbyGb1Ti5VO37f3z7bNjmmf9FMoa43zlRJXFnyemP_s0T-qSyrFzmwmkmCXgdmkoduZb/exec')
        .then(response => {
          this.rows = response.data;
          const usrname = this.rows.find(row => row.username == this.usr);
          if(!usrname){
            alert("帳號錯誤");
            location.reload();
          }else if(usrname.password != this.psw){
            alert("密碼錯誤");
            location.reload();
          }else {
            alert("登入成功！");
            this.$router.push('/');
          }
        })
        .catch(error => {
          alert(error);
        });      
    }
  }
};
</script>

